//这里定义不需要渲染到模板的变量
var stoptimedown = 1;
export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			goodsRow: '',
			options: [],
			userinfo: '',
			pintuanHomeData: '',
			videoStatus: 2,
			videoPath: '',
			specs1: '',
			specs2: '',
			goods_sku_id: 0,
			animagg2: false,
			specs_index1: null,
			specs_index2: null,
		}
	},
	computed: {
		style() {
			var StatusBar = this.StatusBar;
			var CustomBar = this.CustomBar;
			var bgImage = this.bgImage;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});`;
			}
			return style
		}
	},

	//监听页面加载，其参数为上个页面传递的数据，参数类型为Object（用于页面传参）
	onLoad(options) {
		var that = this;
		if (!options.goods_id) {
			uni.showModal({
				title: '温馨提示',
				content: '缺少关键参数',
				showCancel: false,
				success() {
					uni.navigateBack({})
				},
			})
			return;
		}
		that.options = options;
		// 获取用户信息
		that.userinfo = uni.getStorageSync('userinfo') ? uni.getStorageSync('userinfo') : uni.getStorageSync('userInfo')?uni.getStorageSync('userInfo'):'';

		// 获取商品信息
		this.$http.post("goods/show", {
			id: options.goods_id
		}).then((res) => {
			console.log(res.data);
			that.goodsRow = res.data;
			if (res.data.pintuan_list != '') {
				that.setCountDown();
			}
		}).catch(err => {
			console.log('返回错误信息', err);
		})

		console.log('options==', options);
		that.getPintuanhome();
	},
	//监听页面显示。页面每次出现在屏幕上都触发，包括从下级页面点返回露出当前页面
	onShow() {
		this.stoptimedown = 1;
	},
	//监听页面初次渲染完成。注意如果渲染速度快，会在页面进入动画完成前触发
	onReady() {},
	//监听页面隐藏
	onHide() {
		this.stoptimedown = 2;
	},
	//监听页面卸载
	onUnload() {
		this.stoptimedown = 2;
	},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom() {},
	onShareAppMessage() {
		var that = this;
		var path = "/pages/index/index?jump=pages/goods/index&jumpData1=goods_id&jumpData2=" + that.options.goods_id;
		var title = that.goodsRow.row.goods_name;
		var imageUrl = that.goodsRow.row.goods_gallery_images[0];
		return {
			title: title,
			path: path,
			imageUrl: imageUrl,
		}
	},
	methods: {
		judgeGoodsNum() {
			var that = this;
			var goods_id = that.options.goods_id;
			var specs1 = that.specs1;
			var specs2 = that.specs2 ? that.specs2 : 0;
			this.$http.post("judgeGoodsNum",{goods_id:goods_id,specs1:specs1,specs2:specs2},{showLoading:false}).then((res)=>{
				console.log(res.data.id);
				that.goods_sku_id = res.data.id ? res.data.id : 0;
				console.log('goods_sku_id',that.goods_sku_id);
			})

		},
		tishi() {
			var that = this;
			if ((that.specs1 == '' || that.specs2 == '') && that.goodsRow.specs.specs1 && that.goodsRow.specs.specs2) {
				wx.showToast({
					title: '请选择产品型号',
					icon: 'none',
				})
				return;
			} else if (that.goodsRow.specs.specs1 && !that.goodsRow.specs.specs2 && that.specs1 == '') {
				wx.showToast({
					title: '请选择产品型号',
					icon: 'none',
				})
				return;
			} else if (that.goods_sku_num <= 0) {
				wx.showToast({
					title: '该规格暂无库存',
					icon: 'none',
				})
				return;
			}
		},
		// 切换规格
		changeguige(e) {
			var that = this;
			var scrollid = e.currentTarget.dataset.scrollid;
			var id = e.currentTarget.dataset.id;
			var group_id = e.currentTarget.dataset.group_id;
			var index = e.currentTarget.dataset.index;

			that.specs1 = id;
			that.specs_index1 = index;
			console.log('specs_index1',that.specs_index1);
			that.scrollkey = scrollid;
			that.judgeGoodsNum();
		},

		changeguige2(e) {
			var that = this;
			var scrollid = e.currentTarget.dataset.scrollid;
			var id = e.currentTarget.dataset.id;
			var group_id = e.currentTarget.dataset.group_id;
			var index = e.currentTarget.dataset.index;

			that.specs2 = id;
			that.specs_index2 = index;
			console.log('specs_index2',that.specs_index2)
			that.scrollkey = scrollid;
			that.judgeGoodsNum();
		},

		alert_donghua_out(e) {
			var that = this;
			var donghua = wx.createAnimation({
				duration: 0,
				timingFunction: 'linear',
			})
			if (e.currentTarget.dataset.types == 'buy') {

				donghua = donghua.translateY(-560).step();

				that.alert_donghua_out2 = donghua;
				that.animagg2 = true;
				that.showModalStatus = true;
			} else {
				donghua = donghua.translateY(-600).step();

				that.alert_donghua_out = donghua;
				that.animagg = true;
				that.showModalStatus = true;
			}

		},
		alert_donghua_hide(e) {
			var that = this;
			var donghua = wx.createAnimation({
				duration: 500,
				timingFunction: 'linear',
			})
			if (e.currentTarget.dataset.types == 'buy') {
				donghua = donghua.translateY(560).step();

				that.animagg2 = false;
				that.alert_donghua_out2 = donghua;

				console.log('隐藏弹窗')
			} else {
				donghua = donghua.translateY(600).step();

				that.animagg = false;
				that.alert_donghua_out = donghua;

			}

		},

		addformid(e) {
			this.addFormID(e.detail.formId);
		},
		// 获取视频说明地址
		getShuomingVideo() {
			var that = this;
			console.log('获取视频说明地址')
			if (that.videoPath != '') {
				that.videoStatus = 1;
				return;
			}
			this.$http.post('getVideoPath', {
				id: that.goodsRow.row.rule_video
			}).then((res) => {
				console.log('视频地址', res);
				that.videoPath = res.data;
			})
			that.videoStatus = 1;
		},
		closeVideo() {
			var that = this;
			if (that.videoStatus == 1) {
				that.videoStatus = 2;
			} else {
				that.videoStatus = 1;
			}
		},
		//自定义方法
		getPintuanhome() {
			var that = this;
			this.$http.post("pintuan/pintuan_home", {
				limit: 10,
				pages: 0,
				goods_id: that.options.goods_id
			}).then((res) => {
				console.log("往期拼团记录", res.data);
				that.pintuanHomeData = res.data;
			})
		},
		onGotUserInfo(res) {
			var that = this
			console.log("getuserinfo===", res);
			if (res.detail.userInfo) {
				uni.setStorageSync('userInfo', res.detail.userInfo);
				this.upUserInfo(res.detail);
				if(res.currentTarget.dataset.btntype=='buybtn' && ((!that.goodsRow.specs.specs1 && !that.goodsRow.specs.specs2) || that.goods_sku_id>=1)){
					uni.navigateTo({
						url: res.currentTarget.dataset.url
					})
				}
				else{
					console.log('显示规格窗口')
					that.animagg2 = true;
				}
				
			}
		},
		goback() {
			uni.navigateBack({})
		},
		jumpTo(e) {
			var that = this;
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			
			console.log('sku_id',that.goods_sku_id)
			console.log('specs',that.goodsRow.specs.specs1);
			console.log('specs',that.goodsRow.specs.specs2);
			console.log('btntype',e.currentTarget.dataset.btntype)
			
			
			if(e.currentTarget.dataset.btntype=='buybtn' && ( (!that.goodsRow.specs.specs1 && !that.goodsRow.specs.specs2) || (that.goods_sku_id>=1 && that.animagg2==true))  ){
				this.jumpFunction(jumptype, url, closepage);
			}
			else if(e.currentTarget.dataset.btntype == 'jumpto'){
				this.jumpFunction(jumptype, url, closepage);
			}
			else{
				console.log('显示规格窗口')
				that.animagg2 = true;
			}
			
			
		},
		/**
		 * 倒计时
		 */
		setCountDown() {
			let time = 1000;
			let listData = this.goodsRow.pintuan_list;
			let list = listData.map((v, i) => {
				if (v.times <= 0) {
					v.times = 0;
				}
				v.times = v.times * time;
				let formatTime = this.getFormat(v.times);
				v.times -= time
				v.times = v.times / time;
				v.countDown = `${formatTime.hh}:${formatTime.mm}:${formatTime.ss}`;
				return v;
			})
			this.goodsRow.pintuan_list = list;
			if (this.stoptimedown == 1) {
				setTimeout(this.setCountDown, time);
			}

		},
	}
}
